@import url(https://fonts.googleapis.com/css?family=Inter:400,700,600);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Linear Background */
body {
    align-items: flex-start;
    background-size: 100% 100%;
    display: flex;
    position: relative;
	background: linear-gradient(116.82deg, #4539CA 0.43%,  #2EE5A3 98.96%);
	-webkit-animation: gradient 15s ease infinite;
	        animation: gradient 15s ease infinite;
    min-width: 1440px;
	height: 100vh;
    padding: 0 170px;
}

@-webkit-keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

/* Main Card */
.Card {
    position: relative;
    width: 1100px;
    height: 600px;
    left: 170px;
    top: 212px;

    background: #FFFFFF;
    border-radius: 8px;
}

/* Left Dark Blue Rectangle on Card */
.inner-rectangle {
    position: absolute;
    width: 550px;
    height: 600px;
    left: 0;
    top: 0;

    background: #0E0E52;
    border-radius: 8px 0px 0px 8px;
}

/* Right Rectangle on Card -> Modifying Picture */
.melbs-portrait {
    height: 600px;
    left: 575px;
    object-fit: cover;
    position: absolute;
    top: 0;
    width: 500px;
}

/* Title */
.title {
    position: absolute;
    width: 475px;
    height: 50px;
    left: 47px;
    top: 50px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 44px;
    line-height: 53px;
    letter-spacing: 0.1em;
    text-align: left;

    color: #FFFFFF;
}

/* First paragraph */
.first-paragraph {
    position: absolute;
    width: 435px;
