/* Tools Box */
.react-tool-box {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 5px 15px;
    grid-gap: 10px;
    gap: 10px;

    position: absolute;
    width: 40px;
    height: 25px;
    left: 50px;
    top: 435px;

    background: #0E0E52;
    border: 1px solid #FFFFFF;
