/* Say Hi! Button */
a.contact-button {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 10px 30px;
    grid-gap: 10px;
    gap: 10px;

    position: absolute;
    width: 90px;
    height: 35px;
    left: 47px;
    top: 510px;

    background: #192BC2;
    border-radius: 15px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    /* identical to box height */

    letter-spacing: 0.05em;
    text-decoration: none;
    color: #FFFFFF;
}

a.contact-button:hover {
    color: black;
    background-color: white;
    text-decoration: underline;
}

